	module.exports = {"data":{"markdownRemark":{"html":"<p>えー、自宅のWindows PCが死にました。</p>\n<p>Surface Pro とデスクPC (ツクモ BTO をベースとした自作PC) が死にました。<br>\nSurface Pro は電源が死んだのか、本体が死んだのか不明、デスクPCの方は HDD が死んだだけのようです。</p>\n<p>そんなわけで2015年初頭あたりから構想していた、<a href=\"https://twitter.com/danmaq/status/786926861414572033\">自宅PC環境を全部Macに移行する計画を実施しました！</a><br>\n具体的にはこんな感じです。</p>\n<h2>Mac Pro または iMac に環境を全移行する</h2>\n<p> 現在は暫定として Mac mini 2012 への移行で留めています。</p>\n<h2>Mac でできることは Mac でやる</h2>\n<ul>\n<li>ウェブブラウザやメール、その他アクセサリ</li>\n<li>SNS(Skype、Twitter他)</li>\n<li>クラウドストレージ(Dropbox、OneDrive他)</li>\n<li>MS Office</li>\n<li>Adobe一式</li>\n<li>Unity</li>\n<li>Minecraft</li>\n</ul>\n<h2>Windows でしかできないことは、Parallelsで！</h2>\n<p>Parallels はホスト型・ハイパーバイザ型両対応みたいですね。<br>\n当初純ハイパーバイザ型なVM、Veertu を選んでいたのですが、3Dアクセラレーションが完全にCPU持ちっぽくてまだ尚早かな、と判断して断念。\n実際、Veertu だと 3DMARK99 の時点で 20fps くらいだったのが、Parallels だと 3DMARK2001SE まで 60FPS 安定でした。</p>\n<p>(2003辺りから所々でカクつきが見えるようになってくる)</p>\n<ul>\n<li>弥生会計</li>\n<li>Visual Studio</li>\n<li>MikuMikuDance</li>\n<li>東方とか</li>\n</ul>\n<h2>モバイル環境は？</h2>\n<p>ここまで Mac Mac 言っておいてアレですが、Microsoft Surface Pro 4 を買いました！</p>\n<p>まだ Macbook への移行は考えていません。\nスクリーンタッチは割と必須っぽい！</p>\n<p>iPad Pro + VNC でデスクトップ接続なんかも考えたのですが、以下の理由で保留しています。</p>\n<ul>\n<li>モバイル転送量制限 10GB とかあっという間すぎわろた→ミニマム 50GB 辺りから検討かな</li>\n<li>通勤路線としてしばしば使う、錦糸町〜品川間の回線細すぎ(実質電話くらいしかできない)問題→引っ越しが必要</li>\n</ul>\n<h2>Mac に移行して思ったこと</h2>\n<p> あれ？想像していたより辛くなくね？</p>","frontmatter":{"cover":null,"date":"2016-10-23T08:10:18+09:00","strDate":"2016/10/22","title":"自宅環境をMacに移行しました"},"fields":{"langKey":"ja","slug":"/ja/blog/2016/10/23-switched-to-mac/"}}},"pathContext":{"path":"/ja/blog/2016/10/23-switched-to-mac/","langKey":"ja"}}

